@import url(https://fonts.googleapis.com/css2?family=Lobster&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #e6fffd !important;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.headingFirst{
    font-size: 1.5em;
}
.headingFull{
    font-family: 'Lobster', cursive;
    font-size: 3em;
    text-align: center;
    margin-top: 1.5rem;
    font-weight: 100;
    padding: 0.5rem;
}
.fullform{
    font-size: 1em;
    font-weight: 500;
    line-height: normal;
}
#frame2{
    display: none;
}
#frame3{
    display: none;
}
#frame3:hover{
    cursor: pointer;
}
#content{
    display: flex;
    justify-content: space-evenly;
    margin: 1.5rem;
}
.first{
    align-items: center;
}
#trendD{
    margin-top:-0.4rem !important;
    margin-bottom: 0.5rem !important;
}
.changes{
    width: 38vw !important;
}
#soceTrend{
    color: black;
    -webkit-text-decoration: underline solid black;
            text-decoration: underline solid black;
}
#updates{
    width: 25vw;
    border: 2px solid black;
    padding: 0.5rem;
    padding-top: 1rem;
    height: 100%;
    text-align: center;
    background-color: rgb(108, 108, 199, 0.5);
    color: black;
    border-radius: 2rem;
}
#updateItem{
    color:rgb(255, 255, 255)
}
#timeLeft{
    text-align: end;
    margin: 0.5rem;
}
#ulText{
    text-align: justify;
}
li{
    margin-right: 1rem;
    font-family: 'Roboto Mono', monospace;
    font-size: 1rem;
    padding: 0.5rem;
}
li:first-child{
    margin-top: 0.3rem;
}
#pragraph{
    width: 60vw;
    padding: 1rem;
}
#pragraphM{
    width: 90%;
    padding: 1rem;
    margin: 1rem;
}
#pragraph2{
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    padding: 1rem;
}
#individual{
    font-size: 1.3rem;
    margin: 0.2rem;
    padding: 0.5rem;
    font-family: 'Roboto Mono', monospace;
    text-align: justify;
}
#individual2{
    font-size: 0.9rem;
    margin: 0.2rem;
    padding: 0.5rem;
    font-family: 'Roboto Mono', monospace;
}
#green{
    padding: 0.2rem;
    border-radius: 0.2rem;
    background-color: green;
    color: white;
    line-height: 2em;
}
#yellow{
    padding: 0.2rem;
    border-radius: 0.2rem;
    background-color: yellow;
    line-height: 2em;
}
#red{
    padding: 0.2rem;
    border-radius: 0.2rem;
    background-color: red;
    line-height: 2em;
    color: white;
}
#orange{
    padding: 0.2rem;
    border-radius: 0.2rem;
    background-color: orange;
    line-height: 2em;
}
#popOut{
    position: fixed;
    top: 0rem;
    width:80%;
    max-height: 85vh;
    margin: 1rem;
    padding: 1rem 0.5rem;
    border-radius: 2rem;
    color: white;
    background-color: rgb(47, 47, 47);
    z-index: 1;
    overflow-y: auto;
}
#popOut::-webkit-scrollbar {
    display: none;
}
#inner{
    display: flex;
    flex-direction: column;
    align-items: flex-end;
}
.ui.button.vertical.animated{
    font-size: 1.5rem !important;
    width: -webkit-fit-content !important;
    width: -moz-fit-content !important;
    width: fit-content !important;
    height: -webkit-fit-content !important;
    height: -moz-fit-content !important;
    height: fit-content !important;
    padding: 1rem !important;
    margin: 0.5rem !important;
}
.ui.button.vertical.animated .visible.content{
    margin: auto !important;
}
#click{
    text-decoration: underline;
    color: rgb(99, 99, 189);
    cursor: pointer;
}

.howToUse{
    display: flex;
    flex-direction: column-reverse;
    align-items: center;
}
#video{
    margin-right: 0.8rem;
}
#homeVideo{
    display: flex;
    justify-content: space-between;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
}
#frame{
    margin: 0.5rem;
}
.videoHelp{
    display: flex;
    flex-direction: column;
    align-items: center;
}
.videoHelp>p{
    font-size: 1rem;
}
#increaseImg{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: rgb(108, 108, 199, 0.5);
    color: black;
    width: 80%;
    padding: 1rem;
    border-radius: 2rem;
    border: 2px solid;
    margin: auto;
}
#increaseImg>img{
    border: 2px solid;
    border-radius: 2rem;
    margin-top: 0.5rem;
}
#increaseImg>p#heading1{
    font-size: 1.8rem;
    text-align: center;
    color: brown;
    -webkit-text-decoration: underline solid;
            text-decoration: underline solid;
    font-weight: bold;
}
#increaseImg>p#heading2{
    font-size: 1.3rem;
    text-align: center;
    margin-bottom: 0.5rem;
}
#timer1{
    margin-bottom: 1rem;
}
#timer2{
    display: none;
}
#myTimer{
    font-size: 2rem;
    margin: 4rem;
    line-height: 4rem;
}
@media (max-width: 1120px){
    .first #updates{
        width: 40% !important;
    }
    #updates{
        width: 50% !important;
    }
    #pragraph{
        width: 50%;
    }
    .first{
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }
    #timer1{
        display: none;
    }
    #timer2{
        display: block;
    }
}
@media (max-width: 1100px){
    .headingFull{
        font-size: 2.5em;
    }
    .headingFirst{
        font-size: 1em;
    }
}
@media (max-width: 850px){
    #homeVideo{
        flex-direction: column;
    }
}
@media (max-width: 770px) {
    .headingFull{
        font-size: 2em;
    }
    #frame1, #frame{
        display: none;
    }
    #frame2{
        display: flex;
    }
    #content{
        flex-direction: column;
        align-items:center;
    }
    #updates{
        width: 60% !important;
        margin: 1rem;
    }
    #pragraph{
        width: -webkit-fit-content;
        width: -moz-fit-content;
        width: fit-content;
    }
    .changes{
        width: 100% !important;
    }
    .chnages2{
        flex-direction: column-reverse !important;
    }
}
@media (max-width: 640px){
    .headingFull{
        font-size: 1.5em;
    }
    .howToUse{
        flex-direction: column-reverse;
        align-items: center;
    }
    #video{
        margin-right: 0px;
    }
    #myTimer{
        flex-direction: column;
        align-items: center;
    }
    .first #updates{
        width: 60% !important;
    }
}
@media (max-width: 340px){
    #frame1{
        display: none;
    }
    #frame2{
        display: none;
    }
    #frame3{
        display: flex;
    }
    #video{
        border: 0px solid;

    }
    .headingFull{
        margin: 0px !important;
        padding: 0px !important;
    }
}

i.icon:not(.extra){
    display: none !important;
}
.ui.menu.navBar{
    font-size: 1.1rem;
    margin: 0;
    background-color: rgb(0, 0, 255, 0.5) !important;
    width: 100% !important;
    border-radius: 0px;
    padding: 0.5rem 0.2rem;
    color: white;
}
.ui.menu .item{
    color: white !important;
    border-radius: 1rem !important;
}
.ui.menu .active.item, .ui.vertical.menu .active.item{
    font-weight: 700 !important;
    border-radius: 1rem !important;
    color: black !important;
    background-color: #e6fffd !important;
}
.ui.menu :not(.active).item:hover{
    color: black !important;
    background-color: transparent;
}
.navigation{
    display: flex;
    justify-content: center;
}
.ui.dropdown.icon{
    background-color: white !important;
}

.ui.dropdown.icon>.text{
    font-size: 2rem;
    color: black;
    background-color: white !important;
}
.ui.pointing.dropdown.mobile .dropdown.icon{
    margin: 0rem 1rem !important;
}
.ui.dropdown.icon>.menu.visible .item{
    font-size: 1rem !important;
}
.hamburger, .ui.vertical.menu{
    width: 100% !important;
}
.ui.vertical.menu{
    display: block !important;
    background-color: rgb(0, 0, 255, 0.5) !important;
}
.prediction .menu{
    font-size: 1.25rem !important;
}
.ui.vertical.menu .item{
    /* background-color: blue !important; */
    color: white !important;
}
i.icon:hover:not(.extra){
    cursor: pointer;
}
h1:first-child{
    font-size: 4rem !important;
    display: inline !important;
    margin-top: 2rem !important;
}

.useLaptop{
    display: none;
    color: red;
    font-size: 1.5rem;
    padding: 1rem;
}
.heading{
    font-size: 3rem !important;
    margin: 2rem !important;
}
i.dropdown.icon{
    display: inline !important;
    display: initial !important;
}
#menuBlink{
    color: rgb(0, 0, 255);
    -webkit-animation: blink 1s infinite;
            animation: blink 1s infinite;
}
@-webkit-keyframes blink {
    50%{
        opacity: 0.5;
    }
}
@keyframes blink {
    50%{
        opacity: 0.5;
    }
}
@media (max-width: 776px) {
    .heading{
        margin: 1rem !important;
    }
    .navigation{
        justify-content: space-between;
        align-items: center;
    }
    .ui.menu.navBar{
        display: none;
    }
    .ui.vertical.menu{
        font-size: 1.5rem !important;
    }
    i.icon.hamburgerIcon{
        display: block !important;
        margin: 1rem !important;
    }
    .useLaptop{
        display: block;
    }

}
@media (max-width: 1200px){
    .ui.menu.navBar:not(.vertical){
        font-size: 0.9rem;
    }
    .ui.vertical.menu{
        display: none;
    }

}
@media (max-width: 986px){
    .ui.menu.navBar:not(.vertical){
        font-size: 0.7rem;
    }
    .ui.vertical.menu{
        display: none;
    }

}

@media (max-width: 330px) {
    h1:first-child{
        font-size: 2.5rem !important;
        margin-top: 0.5rem !important;
    }
}
